import { Pipe, PipeTransform } from '@angular/core';
import { FrontendLogger } from '@biz-nest/core-client';

@Pipe({
  name: 'nestedData',
  standalone: true
})
export class NestedDataPipe implements PipeTransform {
  transform(data, key: string): string {
    FrontendLogger.debug('NestedDataPipe:transform:: - data: %data, key: %key', data, key);

    if (!data || !key) {
      return '';
    }

    return (data[key] || '') as string;
  }
}
