import { DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import {
  AppNameService,
  EventbusService,
  MainBarTitleService,
  NestedDataPipe,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminListDropdownComponent } from '../../../misc/admin-dropdown/admin-list-dropdown.component';
import { AdminModalComponent } from '../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminPaginatorComponent } from '../../../misc/admin-paginator/admin-paginator.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';
import { AuditLogBase } from '../audit-log.base';

@Component({
  selector: 'app-audit-log-list',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    NestedDataPipe,
    AdminModalComponent,
    AdminPaginatorComponent,
    AdminListDropdownComponent,
    AdminPageContentComponent,
    AdminTopBarComponent
  ],
  templateUrl: './audit-log-list.component.html',
  styleUrl: './audit-log-list.component.scss'
})
export class AuditLogListComponent
  extends AuditLogBase
  implements OnInit, OnDestroy {
  sortStates = {
    id: 'asc',
    info: 'asc',
    date: 'asc'
  };

  constructor(
    protected override windowService: WindowService,
    protected override router: Router,
    protected override eventbus: EventbusService,
    protected override toastr: ToastrService,
    protected override route: ActivatedRoute,
    private apNme: AppNameService,
    protected override mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    super(windowService, router, eventbus, toastr, route, mainBarTitle);
  }

  ngOnInit(): void {
    this.init();
    this.mainBarTitle.title = 'User Activity';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view user activity for your organization to ensure effective tracking and engagement.'
    });
    scrollToTop(this.windowService);
  }

  override ngOnDestroy() {
    super.ngOnDestroy();
  }
}

